      ]
    }
  },

  methods: {
    // 快捷跳转
    jumpTo(type) {
      console.log(type)
      switch (type) {
        case 2:
          this.$router.push('/standBoard')
          break
        case 3:
          this.$router.push('/standard')
          break
        case 4:
          this.$router.push('/affirmCheck')
          break
        case 5:
          this.$router.push('/organization')
          break
        default:
          this.pageType = 1
          break
      }
    }
  },

  mounted() {}
}
